.about__container {
    display: grid;
    grid-template-columns: 50% 50%;
    gap: 15%;
}

h2 {
    padding: 0 0 2rem 0;
}

.about__me {
    width: 70%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent, var(--color-primary),transparent);
    display: grid;
    place-items: center;
}

.about_me_image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about_me_image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-rows: 1fr;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 2px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;

}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/*========================Media queries medium device ===========*/

@media screen and (max-width: 1024px) {
    .about_container {
        grid-template-rows: 1fr;
        gap: 0;
    }
    .about__me {
        width: 70%;
        margin: 2rem auto 4rem;
    }
    .about__content p {
        margin: 1 rem 0 1.5rem;
    }
    .about__card {
        width: 70%;
    }

}

/* ================Media Queries (SMALL DEVICES) ==================== */

@media screen and (max-width: 600px) {
   .about__me {
    width: 65%;
    margin: 0 auto 3rem;
   }
   .about__cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
   }
   .about__card {
    width: 90%;
    text-align: center;
    padding: 1rem;
}
   .about__content{
    text-align: center;
   }
   .about__content p {
    margin: 1.5rem 0;
   }
}
