header {
    height: 100vh;
    padding-top: 2rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}


/* ==============================CTA========================= */

.cta {
    margin-top: 1rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* ==============================Header Socials========================= */

.header__socials {
    display: flex;
    flex-direction: column ;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 2rem;
    bottom: 3rem;
    width: 15rem;
    height: 15rem;    
}

.linkedin {
    padding: 0.9rem;
}
.github {
    padding: 0.9rem;
}

.header__socials::after {
    content: "";
    width: 5px;
    height: 3rem;
    background: var(--color-primary);
}

/*==========================IMG ==================== */

.me {
    background: radial-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 35rem;
    position: relative;
    left: calc(50% - 11rem);
    margin-top: 1.5rem;
    border-radius: 10rem 10rem 10rem 10rem;
    overflow: hidden;
    padding: 0rem;
}


/*========================scrolldown===========*/

.scroll__down {
    position: absolute;
    right: 0rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;
}

/*========================Media queries medium device ===========*/

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

/* ================Media Queries (SMALL DEVICES) ==================== */

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
    .header__socials, .scroll__down {
        display: none;
    }
}
